header {
    position: relative;
    height: 50px; }
nav {
    position: fixed;
    width: 100%;
    height: 53px;
    background-color: #212121;
    z-index: 5; }

.hamburger {
    display: none; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style: none;
    &_link {
        color: #fff;
        font-size: 15px;
        font-weight: 300;
        &:hover {
            text-decoration: none;
            color: $main-color; } }
    &_item {
        border-right: 2px dotted black;
        padding-right: 40px;
        &:last-child {
            border-right: none; } } }
.subheader {
    position: absolute;
    top: 50px;
    width: 100%;
    height: 87px;
    background-color: rgba(0, 0, 0, 0.6);
    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }
    &_official {
        margin-top: 6px;
        color: #fff;
        font-size: 13px;
        font-weight: 400;
        opacity: 0.5; }
    &_call {
        margin-top: 19px;
        color: #fff;
        text-align: right;
        font-size: 12px;
        font-weight: 300; }
    &_phone {
        display: block;
        margin-top: 8px;
        color: #fff;
        text-align: right;
        font-size: 22px;
        font-weight: 400;
        text-transform: uppercase;
        line-height: 12px; }
    &_btn {
        color: #fff;
        text-align: center;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase;
        border-radius: 4px;
        background: linear-gradient(0deg, $main-color 0%, #4ce2ff 100%);
        box-shadow: 0px -3px 0px 0px rgba(0, 0, 0, 0.12) inset;
        width: 172px;
        height: 46px;
        margin-top: 19px;
        @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url(../img/uber_bg.png) center center/cover no-repeat;
    padding: 150px 0 135px 0;
    &_header {
        color: #fff;
        font-size: 60px;
        font-weight: 700;
        line-height: 60px;
        text-transform: uppercase;
        margin: 0;
        text-align: center; }
    &_subheader {
        color: #fff;
        font-size: 36px;
        font-weight: 400;
        line-height: 36px;
        margin: 0;
        margin-top: 22px;
        text-align: center; }
    &_descr {
        color: #fff;
        text-align: center;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        margin-top: 23px; }
    &_btn {
        display: block;
        margin: 118px auto 0 auto;
        width: 259px;
        height: 63px;
        border-radius: 4px;
        border: none;
        background: linear-gradient(0deg, $main-color 0%, #4ce2ff 100%);
        box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, 0.12) inset;
        color: #fff;
        font-size: 18px;
        font-weight: 400;
        text-transform: uppercase;
        @include btn_hover; } }

.reasons {
    padding: 71px 0 65px 0; }
.label {
    position: relative;
    margin: 0 auto;
    height: 24px;
    width: 112px;
    padding: 5px 12px;
    background-color: #000000;
    color: #ffffff;
    font-size: 11px;
    font-weight: 300;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        background-color: #fff;
        color: black;
        &_min {
            width: 76px; } } }
.title {
    position: relative;
    margin-bottom: 0;
    margin-top: 38px;
    color: #222222;
    font-size: 38px;
    line-height: 38px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    z-index: 2;
    &_white {
        color: #fff; } }
.subtitle {
    color: #000;
    position: relative;
    text-align: center;
    font-size: 17px;
    font-weight: 400;
    margin-top: 19px;
    z-index: 2;
    &_white {
        color: #fff; } }


.reasons {
    &_block {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 35px; }
    &_round {
        display: flex;
        justify-content: center;
        align-items: center;
        margin-right: 26px;
        width: 113px;
        height: 113px;
        background-color: $main-color;
        border-radius: 100%; }

    &_descr {
        width: 343px; }
    &_subtitle {
        position: relative;
        line-height: 21px;
        color: $main-color;
        font-size: 17px;
        font-weight: 400; }

    &_text {
        color: #202020;
        font-size: 15px;
        font-weight: 300;
        line-height: 21px; } }


.mobile {
    min-height: 864px;
    padding: 48px 0 79px 0;
    background: url('../icons/mobile_bg.png') center center/cover no-repeat;
    .row {
        margin-top: 32px; }



    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background: url('../img/card1.png') center center/ cover no-repeat; }
        &_2 {
            background: url('../img/card2.png') center center/ cover no-repeat; }
        &_3 {
            background: url('../img/card3.png') center center/ cover no-repeat; }
        &_4 {
            background: url('../img/card4.png') center center/ cover no-repeat; }
        &_5 {
            background: url('../img/card5.png') center center/ cover no-repeat; }
        &_6 {
            background: url('../img/card6.png') center center/ cover no-repeat; }

        &_subtitle {
            color: #FFF;
            text-align: center;
            font-size: 24px;
            font-weight: 400;
            line-height: 31px;
            text-transform: uppercase; }
        &_item {
            color: black; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX(-50%);
            width: 25px;
            height: 25px;
            background-color: #000;
            margin: 0 auto;
            border-radius: 100%;
            padding: 3px;
            cursor: pointer;
            &::before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color;
                transition: .3s all; }
            &::after {
                content: '';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                height: 14px;
                width: 2px;
                background-color: $main-color;
                transition: .3s all; }
            &:hover {
                &::before, &::after {
                    background-color: $second-color; } } } } }

.choice {
    position: relative;
    min-height: 897px;
    padding: 59px 0 137px 0;
    background: url('../img/uuber.png') center center/ cover no-repeat;
    img {
        position: relative;
        display: block;
        margin: 54px auto 0 auto;
        z-index: 2; }

    &_descr {
        text-align: center;
        color: #222;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px; }

    .black {
        position: absolute;
        background-color: #070716;
        height: 499px;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 1; } }

.require {
    min-height: 920px;
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0px; } }
    &_title {
        color: #1ca8c3;
        font-size: 22px;
        font-weight: 400; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-size: 17px;
            font-weight: 300;
            line-height: 24px;
            margin-bottom: 10px;
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/layer_blue.png') center center/ cover no-repeat; } } }
    &_descr {
        margin-top: 25px;
        font-size: 17px;
        font-weight: 300;
        line-height: 24px;
        color: #1ca8c3; }
    .warning {
        .require_title {
            color: $second-color; }
        .require_list {
            li {
                &::before {
                    background: url('../icons/layer_red.png') center center/ cover no-repeat; } } } } }

.world {
    position: relative;
    min-height: 716px;
    padding: 49px 0;
    background: url('../img/world_bg.png') center center/ cover no-repeat;

    &_link {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 49px;
        text-align: center;
        font-size: 15px;
        font-weight: 400;
        line-height: 68px;
        text-transform: uppercase;
        color: #1fbad6;
        &:hover {
            text-decoration: none;
            color: #fff; } } }

footer {
    padding: 14px 23px 39px 23px;
    a {
        text-decoration: none; } }
.footer {
    &_divider {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }
    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px; }
    &_wrapper > div {
        width: 33%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }
    &_social {
        width: 160px;
        display: flex;
        justify-content: space-between;
        &_item {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 0 4px;
            width: 32px;
            height: 32px;
            background-color: #070716;
            border-radius: 100%;
            span {
                color: #fff; } } }
    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                color: #999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_lang {
            display: block;
            margin: 0;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } }
    &_mobile {
        display: flex;
        justify-content: space-around;
        align-items: center;
        width: 300px;
        img {
            width: 95px; } } }
.xs-visible {
    display: none; }
